.head {
    display: flex;
    height: 100vh;
    align-items: center;
}

.form {
    flex: 40% 1;
    /* margin: 30vh 20vh 50vh; */
    margin: 20vh 10vw 10vh 15vw;
    padding: 30px;
    height: -webkit-min-content;
    height: min-content;
    background: #FFFFFF;
    box-shadow: 0px 4px 84px rgba(0, 0, 0, 0.14);
    border-radius: 9px;
    /* font-size: 1.3em; */
}

.form h2 {
    margin: 10px 0px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content; 
    font-weight: 500;
    margin-left: 0px;
    color: #313C61;
}



.form input {
    padding: 4px 8px;
    margin: 20px 0px;
    width: 95.5%;
    height: 40px;
    background: #F4F4F4;
    border-radius: 8px;
    font-size: 1em;
}

.form #submit {
    width: 100%;
    margin: auto;
    color: white;
    font-weight: 700;
    font-size: medium;
    transition: ease-in .2s;
    background: #575CE5;
    border-radius: 8px;
}

.form #submit:hover {
    background: #1419a1;
    ;
}

.flex-hr {
