}

.nav-bar {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    padding: 20px 0px;
}

.nav-bar button {
    margin: 0px 2vw 0px 0px;
    font-size: medium;
    background-image: linear-gradient(45deg, #1419a1, #575ce5);
    height: 40px;
    min-width: 100px;
    width: auto;
    padding: 5px 20px;
    border-radius: 30px;
    color: white;
    transition: transfrom .5s;
    font-family: 'DM Sans';
}

.nav-bar button:hover {
    -webkit-transform: scale(.98);
            transform: scale(.98);
}

.logout-btn {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.dropdown {
    margin: 0px 30px 0px 0px;
    font-size: medium;
    background-image: linear-gradient(45deg, #1419a1, #575ce5);
    height: 41px;
    /* min-width: 80px; */
    width: 41px;
    display: flex;
    /* padding: 5px 1px; */
    border-radius: 50px;
    /* color: white; */
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
    align-items: center;
    justify-content: center;
}

.logout-btn:hover .dropdown-content {
    display: flex;
}

.dropdown-content {
    position: absolute;
    display: none;
    justify-content: flex-end;
    margin-right: 30px;
    margin-top: 46px;
    z-index: 1;
}

.dropdown-content p {
    padding: 10px 20px;
    background-color: aliceblue;
    border-radius: 5px;
    margin-top: -5px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.dropdown-content p:hover {
    background-color: rgb(211, 211, 211);
}

